<template>
  <div>
    <b-table 
      responsive 
      :items="items" 
      :fields="fields">
      <template slot="path" slot-scope="row">
        <b-input-group 
        :prepend="(row.item.path.col_num) % 2 ? 'document' : 'collection'">
          <input 
          type="text" 
          v-model="row.item.path.temp"/>
          <b-input-group-append>
            <b-dropdown>
              <b-dropdown-item @click="row.item.path.temp = userInformation.uid">Auth uid</b-dropdown-item>
            </b-dropdown>
            <b-btn 
            :disabled="row.item.path.temp === ''"
            @click="splits('add', row.index)"
            variant="success"
            >
              enter
            </b-btn>
            <b-btn
            @click="row.item.path.temp = ''"
            variant="danger">
              clear
            </b-btn>
          </b-input-group-append>
        </b-input-group>
        <div>
          {{row.item.path.url}}
          <b-btn 
          v-if="row.item.path.col_num > 0"
          :disabled="!row.item.path.col_num > 0"
          @click="splits('remove', row.index)">
            ←
          </b-btn>
        </div>
      </template>
      <template slot="method" slot-scope="row">
        <b-btn 
        v-for="(method, index) in methods" :key="index" 
        @click="row.item.method = method"
        :variant="method === row.item.method ? 'primary' : 'outline-primary'"
        >
        {{ method }}
        </b-btn>
        <b-input-group 
          v-for="(inputType) in ['key', 'value']" :key="inputType"
          v-if="['update', 'post'].includes(row.item.method)" 
          :prepend="inputType">
          <input 
          type="text" 
          v-model="row.item.payload[inputType]"
          />
        </b-input-group>
      </template>
      <template slot="action" slot-scope="row">
        <b-btn @click="removeRow(row.index)">remove</b-btn>
      </template>
      <template slot="status" slot-scope="row">
        <p>
          => {{ row.item.path.url }}
        </p>
        <b-btn 
        @click="sendRequest(row.index)" 
        v-if="row.item.path.url !== ''">
          {{row.item.method.toUpperCase()}} {{(row.item.path.col_num) % 2 ? 'to List' : 'Doc' }}
        </b-btn>
        <div v-if="typeof row.item.status === 'string'">
          {{ row.item.status }}
        </div>
        <div v-else>
          <b-button 
          @click.stop="info(row.item, row.index, $event.target)">
            Response
          </b-button>
        </div>
      </template>
    </b-table>
    <b-btn 
    @click="addRow()">
      add a row
    </b-btn>
    <b-modal 
    id="modalInfo" 
    @hide="resetModal" 
    :title="modalInfo.title" 
    ok-only>
      <pre>
        {{ modalInfo.content }}
      </pre>
    </b-modal>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
export default {
  name: 'testComponent1',
  data() {
    return {
      items: [],
      fields: [
        {key: 'path', label: 'path'},
        {key: 'method', label: 'method'},
        {key: 'status', label: 'status'},
        {key: 'action', label: 'action'},
      ],
      methods: ['get', 'post', 'update', 'delete'],
      options: [

      ],
      data_test: null,
      modalInfo: { title: '', content: '' }
    }
  },
  mounted() {
    this.init()
  },
  computed: {
    ...mapState(['userInformation']),
  },
  methods: {
    ...mapActions(['fbGet', 'fbUpdate', 'fbPost', 'fbDelete', 'fbQuery']),
    init: function() {
      this.addRow()
    },
    info (item, index, button) {
      this.modalInfo.title = `${item.method.toUpperCase()}: ${item.path.url}`
      this.modalInfo.content = JSON.stringify(item.status, null, 2)
      this.$root.$emit('bv::show::modal', 'modalInfo', button)
    },
    resetModal () {
      this.modalInfo.title = ''
      this.modalInfo.content = ''
    },
    addRow: function() {
      this.items.push(
        {
          path: {
            splits: [],
            col_num: 0,
            temp: '',
            url: ''
          },
          method: 'get',
          payload: {},
          status: '',
        }
      )
    },
    removeRow: function(index) {
      this.items.splice(index, 1)
    },
    sendRequest: async function(index) {
        let item = this.items[index]
        let res = null
        let obj = {}
        Object.assign(obj, {
          [item.payload.key]: item.payload.value
        })
        item.status = 'send'
        switch(item.method){
          case 'get':
            res = await this.fbGet([`${item.path.url}`])
            if(res){
              res.code ? res = res.code : res
            }
          break
          case 'post':
            res = await this.fbPost([`${item.path.url}`, obj])
            if(res){
              res.code ? res = res.code : res
              res.id ? res = `Push ID: ${res.id}` : res
              item.payload = {}
            }
          break
  
          case 'delete':
            res = await this.fbDelete([`${item.path.url}`])
            if(res === undefined){
              res = 'success'
            } else {
              res.code ? res = res.code : res
            }
          break
  
          case 'update':
            res = await this.fbUpdate([`${item.path.url}`, obj])
            if(res === undefined){
              res = 'success'
              item.payload = {}
            } else {
              res.code ? res = res.code : res
            }
          break
  
          default:
          console.log(['invalid method', item.method])
        }
        this.items[index].status = res
    },
    splits: function(type, index) {
      let path = this.items[index].path
      switch(type){
        case 'add':
          path.splits.push(path.temp)
          path.temp = ''
          path.col_num += 1
        break
        case 'remove':
          path.splits.pop()
          path.col_num -= 1
        break
        default:
        console.log(['invalid method', type])
      }
      path.url = path.splits.join('/')
    },
  }
}
</script>
<style lang="stylus" scoped>
p
  white-space nowrap
</style>



